module.exports = "\nheader {\n    height: auto; /*to take height of header */\n    padding: 10px 0;\n    position : fixed;\n    width: 100%;\n    z-index: 999;\n  }\n  .sticky {\n    background: #000;\n    color: #fff;\n  }\n  header i.icon {\n    position: absolute;\n    right: 33px;\n    top: 15px;\n    cursor: pointer;\n    z-index: 10;\n    display: none;\n  }\n  header .logo h2 {\n    font-size: 40px;\n    color: #fff;\n  }\n  header nav .nav-list {\n    list-style: none;\n    margin-bottom: 0px;\n    display: -webkit-box;\n    display: flex; /* to put link gnb b3d */\n    justify-content: space-around;\n  }\n  header nav .nav-list .list a {\n    display: block; /* to add padding and margin*/\n    padding: 15px 0;\n    margin: 9px 0;\n    color: #fff;\n    text-decoration: none; /*when put mouse on link remove line */\n    -webkit-transition: all 0.3s ease-in-out;\n    transition: all 0.3s ease-in-out;\n  }\n  header nav .nav-list .list a:hover {\n    color: #7cb62f;\n  }\n  @media (max-width: 575px) {\n    header {\n      background: #000;\n    }\n    header .logo {\n      text-align: center;\n    }\n    header nav .nav-list {\n      text-align: center;\n      padding: 0;\n      display: none;\n      /* display: block;*/\n    }\n    header i.icon {\n      display: block;\n    }\n  }\n  .active_btn {\n    background: #7cb62f !important;\n  }\n\n\n\n\n\n\n\n/*# sourceMappingURL=data:application/json;base64, */"